@import "./_base";
@import "./_variable";


* {
	padding: 0;
	margin: 0;
	font-family: 'Noto Sans TC', sans-serif; }

img {
	width: 100%;
	vertical-align: top; }

a {
	text-decoration: none; }

html {
	font-size: 20px;
	padding-top: 98px;
	@include rwd(768px) {
		padding-top: 74px; } }

header {
	position: fixed;
	top: 0;
	left: 0;
	z-index: 50;
	width: 100%;
	border-bottom: 8px solid $blue001;
	background-color: $gray003;
	.wrapper {
		max-width: 1350px;
		padding: 0 15px;
		margin: auto;
		font-size: 0;
		.nanya-logo {
			@include dib;
			figure.pic {
				width: 230px; } }
		.main {
			width: calc(100% - 390px);
			@include dib;
			ul.main-menu {
				list-style: none;
				text-align: center;
				li.main-item {
					padding: 32.5px 20px;
					position: relative;
					@include dib;
					.main-a {
						font-size: 0.9rem;
						font-weight: bold;
						color: $gray001;
						cursor: pointer; }
					@include hover {
						ul.sub-menu {
							opacity: 1;
							pointer-events: auto; } }
					ul.sub-menu {
						width: 416px;
						background-color: $blue001;
						position: absolute;
						bottom: 0;
						left: 0;
						transform: translateY(100%);
						opacity: 0;
						pointer-events: none;
						transition: .3s;
						li.sub-item {
							position: relative;
							z-index: 2;
							@include dib;
							a.sub-a {
								padding: 8px 20px;
								font-size: 0.8rem;
								color: $white001;
								display: block; } }
						@include before {
							width: 200vw;
							height: 100%;
							background-color: $blue001;
							z-index: 1;
							@include pstc5; } } } }
			.mobile-lang {
				display: none; } }
		.lang {
			width: 160px;
			position: relative;
			cursor: pointer;
			@include dib;
			@include hover {
				.sub-menu {
					opacity: 1;
					pointer-events: auto; } }
			.svg-box {
				width: 17px;
				fill: $gray002;
				@include dib; }
			p {
				font-size: 0.8rem;
				padding-left: 10px;
				color: $gray002;
				@include dib; }
			.sub-menu {
				background-color: $gray003;
				width: 100px;
				padding: 10px 0;
				text-align: left;
				position: absolute;
				z-index: 10;
				bottom: 0px;
				right: 0px;
				transform: translateY(100%);
				opacity: 0;
				pointer-events: none;
				transition: .3s;
				.sub-item {
					width: 100%;
					display: inline-block;
					.sub-a {
						width: 100%;
						padding: 10px 10px;
						box-sizing: border-box;
						text-align: left;
						background-color: $gray003;
						display: inline-block;
						vertical-align: middle;
						font-size: 0.8rem;
						color: $gray002;
						text-align: center;
						transition: .3s; } } } }
		.ham {
			.line {} } }
	@include rwd(1080px) {
		// border-bottom: 8px solid $blue001
		.wrapper {
			// padding: 0 15px
			.nanya-logo {
				figure.pic {
					width: 145px; } }
			.main {
				width: calc(100% - 290px);
				ul.main-menu {
					li.main-item {
						padding: 34px 6px;
						.main-a {
							font-size: 0.8rem; }
						ul.sub-menu {
							width: 273px;
							li.sub-item {
								a.sub-a {
									padding: 8px 6px;
									font-size: 0.7rem; } } } } } }
			.lang {
				width: 145px;
				.svg-box {
 }					// width: 17px
				p {
					font-size: 0.7rem;
 }					// padding-left: 10px
				.sub-menu {
					// padding: 10px 0
					.sub-item {
						.sub-a {
							// padding: 10px 10px
							font-size: 0.7rem; } } } }
			.ham {
				.line {} } } }
	@include rwd(768px) {
		border-bottom: 4px solid $blue001;
		.wrapper {
			// padding: 0 15px
			.nanya-logo {
				height: 70px;
				position: relative;
				figure.pic {
					width: 145px;
					position: absolute;
					top: 50%;
					transform: translateY(-50%); } }
			.main {
				display: none;
				width: 100vw;
				height: 100vh;
				padding-top: 70px;
				box-sizing: border-box;
				background-color: $white001;
				position: absolute;
				top: 0;
				left: 0;
				z-index: 1;
				@include before {
					width: 100%;
					height: 4px;
					background-color: $blue001; }
				.close {
					width: 50px;
					height: 50px;
					position: absolute;
					top: 10px;
					right: 10px;
					transform: rotateZ(45deg);
					cursor: pointer;
					&:before,&:after {
						content: '';
						display: block;
						border-radius: 2px;
						background-color: $gray004;
						@include pstc0; }
					&:before {
						width: 30px;
						height: 4px; }
					&:after {
						width: 4px;
						height: 30px; } }
				ul.main-menu {
					width: 72%;
					padding-top: 30px;
					margin: auto;
					text-align: left;
					li.main-item {
						display: block;
						padding: 0px;
						border-bottom: 1px solid $blue001;
						.main-a {
							display: block;
							padding: 10px 5px;
							font-size: 0.9rem; }
						&:nth-child(3) {
							.main-a {
								@include before {
									width: 8px;
									height: 10px;
									background: url('../images/nacmenu-link.svg') right top no-repeat;
									background-size: 6.5px 30px;
									transform: rotate(90deg);
									position: absolute;
									top: 0;
									bottom: 0;
									right: 0;
									margin: auto; } } }
						ul.sub-menu {
							display: none;
							opacity: 1;
							width: 100%;
							position: static;
							transform: none;
							background-color: $white001;
							border-top: 4px solid $blue001;
							&:before {
								display: none; }
							li.sub-item {
								display: block;
								a.sub-a {
									padding: 5px;
									font-size: 0.9rem;
									color: $gray001; } } } } }
				.mobile-lang {
					width: 72%;
					margin: auto;
					padding-top: 10px;
					display: block;
					position: relative;
					.svg-box {
						width: 17px;
						fill: $gray002;
						@include dib; }
					p {
						font-size: 0.9rem;
						padding-left: 10px;
						color: $gray002;
						@include dib; }
					.sub-menu {
						display: none;
						margin-left: 5rem;
						.sub-item {
							.sub-a {
								padding: 10px 0;
								font-size: 0.8rem;
								color: $gray002; } } } } }
			.lang {
				display: none; }
			.ham {
				width: 20px;
				height: 70px;
				cursor: pointer;
				float: right;
				position: relative;
				.line {
					background-color: $gray004;
					height: 3px;
					border-radius: 1.5px;
					@include pstc0;
					&:nth-child(1) {
						width: 18px;
						transform: translate(-1px,-5px); }
					&:nth-child(2) {
						width: 20px; }
					&:nth-child(3) {
						width: 16px;
						transform: translate(-2px,5px); } } } } } }


footer {
	.main-footer {
		padding: 70px 0;
		background-color: $gray003;
		.wrapper {
			font-size: 0;
			max-width: 1360px;
			margin: auto;
			padding: 0 20px;
			box-sizing: border-box;
			.content {
				width: 25%;
				box-sizing: border-box;
				margin-bottom: 30px;
				padding-right: 30px;
				display: inline-block;
				vertical-align: top;
				text-align: left;
				&.global {}
				&.product {}
				&.info {
					width: 27%; }
				&.company {
					width: 23%;
					padding-right: 0; }
				.title {
					color: $gray001;
					font-size: 1rem;
					font-weight: 500;
					padding-bottom: 10px;
					margin-bottom: 10px;
					border-bottom: 1px solid $gray004;
					position: relative;
					&:before {
						content: '';
						display: block;
						width: 85px;
						height: 4px;
						background-image: linear-gradient(15deg,#3574b5 0%,#2189f4 100%);
						position: absolute;
						bottom: -2px;
						left: 0; } }
				&:nth-child(1) {
					.title {
						&:before {
							content: '';
							display: block;
							width: 135px; } } }
				&:nth-child(4) {
					.title {
						width: 100%; } }

				a.map {
					display: block;
					padding-top: 30px;
					.pic {
						fill: $gray004; } }
				.link-box {
					font-size: 0;
					padding-top: 7px;
					.item {
						width: 100%;
						font-size: 0.8rem;
						color: $gray001;
						padding: 7px 10px 7px 0;
						display: inline-block;
						vertical-align: middle;
						box-sizing: border-box; } }

				.info-box {
					padding-top: 7px;
					a.item {
						display: block;
						width: 100%;
						padding: 7px 0;
						.icon {
							width: 15px;
							height: 15px;
							padding-left: 4px;
							display: inline-block;
							vertical-align: top;
							fill: $gray001; }
						.txt {
							width: calc( 100% - 30px );
							padding-left: 10px;
							font-size: 0.8rem;
							color: $gray001;
							display: inline-block;
							vertical-align: top; } } }

				.company-box {
					padding-top: 6px;
					font-size: 0;
					text-align: left;
					a.item {
						// margin-top: 6px
						// width: 79px
						width: calc((100% - 16px)/4);
						padding: 6px 0;
						background-color: $white001;
						border: 1px solid $gray004;
						display: inline-block;
						box-sizing: border-box;
						border-radius: 5px;
						overflow: hidden;
						vertical-align: top;
						margin-left: 3px;
						&:nth-child(4n+1) {
							margin-left: 0; }
						&:nth-child(4n) {
							// margin-top: -5px
							margin-bottom: 0;
							margin-left: 8px;
							padding: 0;
							background-color: rgba($white001,0);
							border: none; }
						figure.company {
							// width: 47px
							width: 60%;
							margin: auto; }
						&:nth-child(1) {
							padding: 0;
							figure.company {
								width: 100%; } }
						figure.QRCode {
							width: 50px;
							margin: auto; }
						.name {
							font-size: 0.6rem;
							color: $gray001;
							text-align: center;
							padding-top: 5px;
							letter-spacing: -1px; } } } } } }
	.sub-footer {
		background-color: $blue001;
		.wrapper {
			max-width: 1360px;
			margin: auto;
			padding: 0 20px;
			box-sizing: border-box;
			margin: auto;
			.box {
				font-size: 0.75rem;
				padding: 20px 0 15px;
				@include clearfix;
				.copyright {
					color: $white001;
					// float: left
					width: 100%;
					text-align: center;
					line-height: 1.3; }
				.txt {
					color: $white001;
					line-height: 1.3; }
				& + .box {
					border-top: 1px solid $white001;
					padding: 15px 0 20px; } } } }

	// @include rwd(1280px)
	// 	.main-footer
	// 		// padding: 70px 0
	// 		.wrapper
	// 			width: 620px
	// 			.content
	// 				width: 50%
	// @include rwd(1120px)
	// 	.sub-footer
	// 		.wrapper
	// 			width: 900px
	@include rwd(960px) {
		.main-footer {
			.wrapper {
				max-width: 590px;
				.content {
					width: 50%;
					margin-bottom: 15px;
					vertical-align: top;
					&.info,&.company {
						width: 50%; } } } }
		.sub-footer {
			.wrapper {
				max-width: 590px;
				.box {
					.copyright {
						float: none;
						margin-bottom: 20px;
						text-align: center; }
					.right_content {
						float: none;
						text-align: center; } } } } }
	@include rwd(640px) {
		.main-footer {
			// padding: 70px 0
			.wrapper {
				width: 350px;
				padding: 0 15px;
				.content {
					width: 100%;
					padding-right: 0;
					&.global {
						width: 100%; }
					&.product {
						width: 100%; }
					&.info {
						width: 100%; }
					&.company {
						width: 100%; }
					.title {
						width: 100%; }
					a.map {
						.pic {
							width: 100%; } }
					.company-box {
						a.item {
 } }							// width: 100px
					&:nth-child(4) {
						.title {
							width: 100%; } } } } }
		.sub-footer {
			.wrapper {
				width: 320px;
				.box {
					// .copyright
					.right_content {
						.link-box {
							a.item {
								margin: 0 5px; } }
						.len {
							display: none; } } } } } }
	@include rwd(374px) {
		.main-footer {
			// padding: 70px 0
			.wrapper {
				width: 100%;
				padding: 0 10px;
				.content {
					.company-box {
						a.item {
							// width: 100px
							.name {
								letter-spacing: -2px; } } } } } }
		.sub-footer {
			.wrapper {
				width: 270px;
				.box {
					// .copyright
					.right_content {
						.link-box {
							a.item {
								margin: 0 4px; } }
						.len {
							display: none; } } } } } } }
